import { createBrowserRouter, Outlet } from 'react-router-dom';
import './App.css';
import Allcourse from './component/Allcourse';
import Backbtn from './component/Backbtn';
import Home from './component/Home';
import Login from './component/Login';
import Mycourse from './component/Mycourse';
import Preview from './component/Preview';
import Profile from './component/Profile';
import Signup from './component/Signup';
import Vidcontent from './component/Vidcontent';

const Router = createBrowserRouter([
  {
    path: "/",
    element: <App />,
    children: [
      { path: "/home", element: <Home /> },
      { path: "/login", element: <Login /> },
      { path: "/signup", element: <Signup /> },
      { path: "/preview", element: <Preview /> },
      { path: "/vidcontent", element: <Vidcontent /> },
      { path: "/allcourse", element: <Allcourse /> },
      { path: "/mycourse", element: <Mycourse /> },
      { path: "/profile", element: <Profile /> },
      { path: "/backbtn", element: <Backbtn /> },
    ],
  },
]);

function App() {
  return (
    <Outlet />
  );
}

export default Router;
